.nav-container {
  display: flex;
  padding: 1.4rem 8.2rem;
  margin: 0.8rem;
  border-radius: 12px;
  background-color: rgba(34, 184, 207, 0.5);
}
.logo-container {
  display: flex;
  align-items: center;
  gap: 1.8rem;
  margin-right: auto;
}
.logo-text {
  font-size: 1.6rem;
  color: #fff;
  font-style: italic;
}
.nav-links {
  list-style: none;
  display: flex;
  gap: 4.2rem;
  align-items: center;
}

.nav-links li a {
  text-decoration: none;
  font-size: 2.2rem;
  font-weight: 400;
  letter-spacing: 1.2px;
  color: #fff;
}

.nav-container img {
  height: 64px;
  width: 64px;
  margin-right: auto;
  border-radius: 12px;
  box-shadow: 0px 8px 36px rgba(0, 0, 0, 0.1);
}

.nav-link:hover {
  /* background-color: #f03e3e; */
  border-bottom: 6px solid #ffa8a8;
  border-radius: 12px;
  padding: 0.5rem;
}

.call-us {
  display: flex;
  align-items: center;
  font-size: 2rem !important;
  font-weight: 500;
  color: #fff !important;
}
.phone-icon {
  /* color: white; */
  height: 24px;
  width: 24px;
  margin-right: 1.2rem;
  transform: rotate(10deg);
  /* color: black !important; */
}

.call-us a:hover {
  border-bottom: 6px solid #99e9f2;
  border-radius: 12px;
  padding: 0.5rem;
}

@media (max-width: 768px) {
  .nav-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    padding-left: 1.8rem;
  }
  .nav-img {
    margin: 5.2rem 0;
  }

  .nav-links {
    display: flex;
    /* background-color: red;
     */

    flex-direction: column;

    align-items: flex-start;
    gap: 5.2rem;
    width: 100%;
    padding-left: 1.2rem;
  }

  .call-us {
    display: flex;
    /* flex-direction: column; */
    gap: 2.5rem;
    max-width: 100%;
    padding: 0;
  }
  .phone-icon {
    align-self: flex-start;
    margin: 0;
  }
}
