.modal {
  position: fixed;
  top: 15vh;
  left: 5%;
  width: 90%;
  background-color: white;
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.modal p {
  font-size: 1.8rem;
}

@media (max-width: 768px) {
  .modal {
    width: 80%;
    left: 10%;
  }
}
